<template>
  <v-app>
    <v-app-bar
      app
      :color="toolbarColor"
      elevate-on-scroll
    >
      <v-toolbar-title v-if="!this.$vuetify.theme.dark" class="display-1">
        <kbd v-if="this.globalWidth<470">{{shortName}}<div class="animate-flicker">&#x2582;</div></kbd>
        <kbd v-if="this.globalWidth>=470">{{myName}}<div class="animate-flicker">&#x2582;</div></kbd>
      </v-toolbar-title>

      <v-toolbar-title v-if="this.$vuetify.theme.dark" class="display-1">
        <code v-if="this.globalWidth<470">{{shortName}}<div class="animate-flicker">&#x2582;</div></code>
        <code v-if="this.globalWidth>=470">{{myName}}<div class="animate-flicker">&#x2582;</div></code>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <div v-if="$vuetify.breakpoint.mdAndUp">
        <v-btn text to="Inicio" link>
          Inicio
        </v-btn>
        <div style="display:inline" class="pr-2"/>
        <v-btn text to="Acerca" link>
          Sobre mí
        </v-btn>
        <div style="display:inline" class="pr-2"/>
        <v-btn text to="Proyectos" link>
          Proyectos
        </v-btn>
        <div style="display:inline" class="pr-2"/>
        <v-btn text to="Contacto" link>
          Contacto
        </v-btn>
      </div>
      <div style="display:inline" class="pr-2"/>
      <v-btn icon @click="themeSwap">
        <v-icon>{{this.iconString}}</v-icon>
      </v-btn>
      <v-menu
        v-if="$vuetify.breakpoint.smAndDown"
        left
        bottom
        transition="scale-transition"
        origin="top right"
      >
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list :color="this.$accentColor" light>
          <v-list-item
          link
          to="Inicio"
          >
            <v-list-item-title>Inicio</v-list-item-title>
          </v-list-item>
          <v-list-item
          to="Acerca"
          link
          >
            <v-list-item-title>Sobre mí</v-list-item-title>
          </v-list-item>
          <v-list-item
          to="Proyectos" link
          >
            <v-list-item-title>Proyectos</v-list-item-title>
          </v-list-item>
          <v-list-item
          to="Contacto" link
          >
            <v-list-item-title>Contacto</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
    <v-content class="pa-0 ma-0">
      <v-container class="pa-0 ma-0" fluid v-scroll="onScroll">
        <transition name="scroll-x-transition" mode="out-in">
          <router-view />
        </transition>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>

export default {
  name: 'App',

  data: () => ({
    globalWidth:window.innerWidth,
    myName:"Vinicio Samayoa",
    shortName:"V.S.",
    toolbarColor:"transparent",
    isDark:true,
    iconString:""
  }),
  methods:{
    onScroll(){
      if(window.scrollY>20){
        if(this.isDark){
          this.toolbarColor="dark";
        }else{
          this.toolbarColor="white";
        }
      }else{
        this.toolbarColor="transparent";
      }
    },
    themeSwap(){
      this.isDark=!this.isDark;

      if(this.isDark){
        this.iconString='mdi-brightness-7';

        if(window.scrollY>20){
          this.toolbarColor="dark";
        }else{
          this.toolbarColor="transparent";
        }
      }else{
        this.iconString='mdi-brightness-6';

        if(window.scrollY>20){
          this.toolbarColor="white";
        }else{
          this.toolbarColor="transparent";
        }
      }

      this.$vuetify.theme.dark=this.isDark;
    }
  },
  mounted() {
    this.isDark=this.$vuetify.theme.dark;
    
    if(this.isDark){
      this.iconString="mdi-brightness-7";
    }else{
      this.iconString="mdi-brightness-6";
    }

    window.onresize = () => {
      this.globalWidth=window.innerWidth;
    }
  },
  beforeCreate(){
    this.isDark=this.$vuetify.theme.dark;
    
    if(this.isDark){
      this.iconString="mdi-brightness-7";
    }else{
      this.iconString="mdi-brightness-6";
    }
  }

};
</script>
<style scoped>
@keyframes flickerAnimation {
  0%   { opacity:1; }
  50%  { opacity:0; }
  100% { opacity:1; }
}
@-o-keyframes flickerAnimation{
  0%   { opacity:1; }
  50%  { opacity:0; }
  100% { opacity:1; }
}
@-moz-keyframes flickerAnimation{
  0%   { opacity:0; }
  100% { opacity:1; }
}
@-webkit-keyframes flickerAnimation{
  0%   { opacity:1; }
  50%  { opacity:0; }
  100% { opacity:1; }
}
.animate-flicker {
   -webkit-animation: flickerAnimation 1.1s infinite forwards step-end;
   -moz-animation: flickerAnimation 1.1s infinite forwards step-end;
   -o-animation: flickerAnimation 1.1s infinite forwards step-end;
    animation: flickerAnimation 1.1s infinite forwards step-end;
    display: inline;
}
.active-dark-class{
  color: aqua;
  border-color: white;
}
</style>