import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import Start from '../src/components/Start'
import AboutMe from '../src/components/AboutMe'
import MyWork from '../src/components/MyWork'
import ContactMe from '../src/components/ContactMe'
import VueRouter from 'vue-router'

Vue.config.productionTip = false

Vue.use(VueRouter)

const routes = new VueRouter({
  base:"/",
  routes: [
      {path: '/', redirect:'Inicio'},
      {path: '/Inicio', component: Start,name:'Inicio'},
      {path: '/Acerca', component: AboutMe,name:'Acerca'},
      {path: '/Proyectos', component: MyWork,name:'Proyectos'},
      {path: '/Contacto', component: ContactMe,name:'Contacto'},
  ],
})

new Vue({
  vuetify,
  router:routes,
  render: h => h(App),
  beforeCreate(){
    this.$vuetify.theme.dark=true;
    Vue.prototype.$accentColor="amber lighten-1";
  }
}).$mount('#app')
